.Progress {
  border-radius: 0.375rem 0.375rem 0 0;
  display: block;
  height: 0.75rem;
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%; }
  .Progress progress[value] {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    border: none;
    display: block;
    height: 0.75rem;
    width: 100%; }
    .Progress progress[value]::-webkit-progress-bar {
      background: #b6f2e4;
      border: none;
      overflow: hidden; }
    .Progress progress[value]::-moz-progress-bar {
      background: #19cca3; }
    .Progress progress[value]::-webkit-progress-value {
      -webkit-transition: all 0.3s cubic-bezier(0.2, 1, 0.2, 1);
      -o-transition: all 0.3s cubic-bezier(0.2, 1, 0.2, 1);
      transition: all 0.3s cubic-bezier(0.2, 1, 0.2, 1);
      background: #19cca3; }



